<template>
  <div class="container py-24" v-if="loading">
    <h1>
      Working on it 🔎
    </h1>
  </div>
  <div
    v-else-if="photo && Object.keys(photo).length > 0"
    class="container pt-12"
  >
    <div class="row">
      <div class="col m7 md:hidden">
        <img class="w-full" :src="photo.urls.regular" alt="" />
      </div>
      <div class="col m5 text-lg grey-text text-darken-3">
        <h1 class="text-3xl">
          <a
            class="inline-flex items-center grey-text text-darken-3"
            :href="photo.user.links.html"
            target="_blank"
            rel="noopener noreferrer"
            ><img
              class="mr-3 rounded-full"
              width="50"
              :src="photo.user.profile_image.medium"
              :alt="fullName + ' profile image'"
            />
            {{ fullName }}
            <!-- <i class="material-icons right">link</i> -->
          </a>
        </h1>
        <p v-if="photo.description">{{ photo.description }}</p>

        <p v-if="photo.location.title">{{ photo.location.title }}</p>
        <!-- <p v-if="photo.location.name">{{ photo.location.name }}</p> -->
        <p v-if="photo.location.country">{{ photo.location.country }}</p>

        <a
          :href="photo.links.download"
          target="_blank"
          rel="noopener noreferrer"
          class="btn my-10"
        >
          Download
          <i class="material-icons right">cloud_download</i>
        </a>

        <div class="mb-10">
          <!-- <p>Uploaded on: {{ formattedDate }}</p> -->
          <span class="btn grey darken-4 mr-2 mb-2"
            >{{ formattedDate }}
            <i class="material-icons left">access_time</i>
          </span>

          <!-- <span class="btn grey darken-4 mr-2 mb-2"
            ><span
              class="color-circle"
              :style="'background-color: ' + photo.color"
            ></span>
          </span> -->

          <!-- <p class="flex items-center my-2">
            Color:<span
              class="color-circle"
              :style="'background-color: ' + photo.color"
            ></span>
          </p> -->
          <span class="btn grey darken-4 mr-2 mb-2"
            >{{ photo.likes }}
            <i class="material-icons left">favorite_border</i>
          </span>
          <!-- <p>Likes: {{ photo.likes }}</p> -->
          <span class="btn grey darken-4 mr-2 mb-2"
            >{{ photo.views }}
            <i class="material-icons left">visibility</i>
          </span>
          <!-- <p>Views: {{ photo.views }}</p> -->
          <span class="btn grey darken-4 mr-2 mb-2"
            >{{ photo.downloads }}
            <i class="material-icons left">cloud_download</i>
          </span>
          <!-- <p>Downloads: {{ photo.downloads }}</p> -->
        </div>

        <div class="tags">
          <span
            v-for="tag in photo.tags"
            :key="tag.title"
            class="btn-small white grey-text text-darken-4 mr-2 mb-2"
            >{{ tag.title }}
          </span>
        </div>
      </div>
      <div class="col m7 hidden md:block">
        <img class="w-full" :src="photo.urls.regular" alt="" />
      </div>
    </div>
  </div>
  <div class="container py-24" v-else>
    <h1>
      This photo does not exist 😢
    </h1>
  </div>
</template>

<script>
import axios from 'axios';
import moment from 'moment';

export default {
  name: 'Photo',

  data() {
    return {
      photo: {},
      loading: true,
    };
  },

  computed: {
    fullName: function() {
      let name = this.photo.user.first_name;
      if (this.photo.user.last_name) {
        name += ' ' + this.photo.user.last_name;
      }
      return name;
    },

    formattedDate: function() {
      return moment(this.photo.date).format('MMM DD, YYYY');
    },
  },

  created() {
    axios
      .get('https://api.unsplash.com/photos/' + this.$route.params.id)
      .then((res) => {
        this.photo = res.data;
      })
      .finally(() => {
        this.loading = false;
      });
  },
};
</script>

<style lang="scss"></style>
