<template>
  <div class="container">
    <div class="gallery">
      <GalleryItem v-for="photo in photos" :key="photo.id" :photo="photo" />
    </div>
    <div class="text-center">
      <div class="btn-large grey darken-4 my-10" v-on:click="onLoadMore">
        Load more <i class="material-icons right">arrow_forward</i>
      </div>
    </div>
  </div>
</template>

<script>
import GalleryItem from './GalleryItem';

export default {
  name: 'Gallery',

  components: {
    GalleryItem,
  },

  props: {
    photos: Array,
  },

  methods: {
    onLoadMore() {
      this.$emit('loadMore');
    },
  },
};
</script>

