<template>
  <div>
    <Search v-on:inputSubmit="onSearch" />
    <div class="container">
      <h1>{{ heading }}</h1>
    </div>
    <div class="container py-12" v-if="loading">
      <h2>
        Working on it 🔎
      </h2>
    </div>
    <Gallery
      v-else-if="photos && Object.keys(photos).length > 0"
      v-on:loadMore="onLoadMore"
      :photos="photos"
    />
    <div v-else class="container py-24">
      <h1>Nothing Found 😥</h1>
    </div>
  </div>
</template>

<script>
import Gallery from '@/components/Gallery';
import Search from '@/components/Search';
import axios from 'axios';

export default {
  name: 'home',

  components: {
    Gallery,
    Search,
  },

  data() {
    return {
      photos: null,
      heading: 'Latest photos',
      nextPage: 1,
      apiUrl: 'https://api.unsplash.com/photos?',
      loading: true,
    };
  },

  methods: {
    onSearch(input) {
      if (input.length > 1) {
        this.apiUrl =
          'https://api.unsplash.com/search/photos?query=' + input + '&';

        axios.get(this.apiUrl).then((res) => {
          this.photos = res.data.results;
          this.heading = `"${input}" photos`;
          this.nextPage = 2;
        });
      }
    },

    onLoadMore() {
      // this.loading = true;
      axios.get(this.apiUrl + 'page=' + this.nextPage).then((res) => {
        console.log(this.apiUrl + 'page=' + this.nextPage);
        if (res.data.results) {
          this.photos = this.photos.concat(res.data.results);
        } else {
          this.photos = this.photos.concat(res.data);
        }

        const uniquePhotos = Array.from(
          new Set(this.photos.map((p) => p.id)),
        ).map((id) => {
          return this.photos.find((p) => p.id === id);
        });

        this.photos = uniquePhotos;

        this.nextPage = this.nextPage + 1;
      });
      // .finally(() => {
      //   this.loading = false;
      // });
    },
  },

  created() {
    axios
      .get(this.apiUrl)
      .then((res) => {
        this.photos = res.data;
        this.nextPage = 2;
      })
      .finally(() => {
        this.loading = false;
      });
  },
};
</script>

